

.gallery .buttons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 0.3em;
}


.gallery img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.images {
  
    display: grid;
    padding: 1em;
    grid-gap: 1em;
    grid-template-rows:repeat(100, 20em);

    background-color: var(--gallery-background);
}

.images.one-column {
    grid-template-columns: repeat(1, 1fr);
}

.images.two-column {
    grid-template-columns: repeat(2, 1fr);
}
.images.three-column {
    grid-template-columns: repeat(3, 1fr);
}

.gallery .bordered {
    border: 2px solid gray;
    border-radius: 1em;
    margin: 0.5em;
}